@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

html,
body {
  font-family: "Roboto";

  height: 100vh;
  margin: 0;
  padding: 0;

  justify-content: center;
}

body {
  overflow-x: hidden;
}
.App {
  display: flex;
  justify-content: center;
  width: 100%;

  height: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}
